
.v-nav {
  position: fixed;
  top: 32%;
  right: 5px;
}
.v-nav .item {
  width: 12px;
  height: 12px;
  text-align: center;
  line-height: 32px;
  border-radius: 50%;
  margin-top: 10px;
  position: relative;
  left: 10px;
  color: #fff;
  background: rgba(0, 0, 0, 0.4);
  cursor: pointer;
  font-size: 0;
}
.v-nav .item.active {
  width: 32px;
  height: 32px;
  font-size: 16px;
  left: 0;
}
.v-nav .item:hover {
  -webkit-animation: navFade 1s;
          animation: navFade 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes navFade {
100% {
    width: 32px;
    height: 32px;
    font-size: 16px;
    left: 0;
}
}
@keyframes navFade {
100% {
    width: 32px;
    height: 32px;
    font-size: 16px;
    left: 0;
}
}



