
.icon-arrow_lift {
  position: absolute;
  bottom: 20px;
  left: 50%;
  margin-left: -20px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 24px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-animation: roll 1.5s infinite;
          animation: roll 1.5s infinite;
}
@media screen and (max-width: 500px) {
.icon-arrow_lift {
    margin-left: -12px;
}
}
@-webkit-keyframes roll {
0% {
    bottom: 20px;
    opacity: 1;
}
100% {
    bottom: 10px;
    opacity: 0;
}
}
@keyframes roll {
0% {
    bottom: 20px;
    opacity: 1;
}
100% {
    bottom: 10px;
    opacity: 0;
}
}



