}
.page .content .section2 .about-wrapper .about-item .item-img:after {
  content: '';
  position: absolute;
  left: 0;
  -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1);
          box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1);
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  width: 70px;
  height: 70px;
  border-radius: 50%;
}
