}
.page .content .section2 .about-wrapper .about-item .item-img:hover {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.page .content .section2 .about-wrapper .about-item .item-img:hover:after {
  -webkit-animation: effect 1s ease-out 75ms;
          animation: effect 1s ease-out 75ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes effect {
0% {
    opacity: 0.3;
}
40% {
    opacity: 0.5;
    -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #109085, 0 0 0 10px rgba(255, 255, 255, 0.5);
            box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #109085, 0 0 0 10px rgba(255, 255, 255, 0.5);
}
100% {
    -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #109085, 0 0 0 10px rgba(255, 255, 255, 0.5);
            box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #109085, 0 0 0 10px rgba(255, 255, 255, 0.5);
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0;
}
}
@keyframes effect {
0% {
    opacity: 0.3;
}
40% {
    opacity: 0.5;
    -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #109085, 0 0 0 10px rgba(255, 255, 255, 0.5);
            box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #109085, 0 0 0 10px rgba(255, 255, 255, 0.5);
}
100% {
    -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #109085, 0 0 0 10px rgba(255, 255, 255, 0.5);
            box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #109085, 0 0 0 10px rgba(255, 255, 255, 0.5);
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0;
}
}
