  border: 1px solid #fff;
  border-radius: 50%;
  width: 42px;
  height: 42px;
  text-align: center;
  line-height: 42px;
  color: #fff;
  font-size: 22px;
  cursor: pointer;
}
.page .content .section6 .text-wrapper .icon:last-child {
  margin-right: 0;
}
.page .content .section6 .bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.page .content .section6 .bottom .text {
  color: rgba(7, 17, 27, 0.3);
  font-size: 14px;
  text-align: center;
  line-height: 50px;
}



